import React, { useEffect } from 'react';
import DictationTool from '../DictationTool';
import './EmbeddedStyles.css';

/**
 * Enhanced version of DictationTool for embedded environments like Teachable
 * - Ensures proper styling
 * - Makes keyboard shortcuts visible
 * - Handles parent frame communication
 */
const EmbeddedDictationTool = (props) => {
  // Send height updates to parent frame
  useEffect(() => {
    const sendHeightUpdate = () => {
      try {
        const height = document.body.scrollHeight;
        window.parent.postMessage({ 
          type: 'DICTATION_HEIGHT_UPDATE', 
          height
        }, '*');
        
        console.log('Sent height update to parent:', height);
      } catch (e) {
        console.error('Error sending height to parent:', e);
      }
    };
    
    // Send initial height after a short delay to ensure rendering
    setTimeout(sendHeightUpdate, 500);
    
    // Set up interval to periodically update height
    const heightInterval = setInterval(sendHeightUpdate, 1000);
    
    // Set up resize observer for more immediate updates
    const resizeObserver = new ResizeObserver(() => {
      sendHeightUpdate();
    });
    
    // Observe body element
    resizeObserver.observe(document.body);
    
    return () => {
      clearInterval(heightInterval);
      resizeObserver.disconnect();
    };
  }, []);
  
  // Override props for embedded mode
  const embeddedProps = {
    ...props,
    // Force keyboard shortcuts to be visible in embedded mode
    hideShortcuts: false,
    // Allow showing UI debug info in embedded mode
    showDebugInfo: true,
  };
  
  return <DictationTool {...embeddedProps} />;
};

export default EmbeddedDictationTool;
